(function () {
    'use strict';
    angular.module('ion-photo').factory('PhotoFilter', PhotoFilterFactory);

    function PhotoFilterFactory($rootScope, $q, $ionicModal) {

        return {
            load: modalFilter
        };

        function modalFilter(image) {
            var defer    = $q.defer();
            var template = '<ion-modal-view class="modal-capture"><ion-header-bar class="bar-light"><button class="button button-clear button-icon ion-ios-close-empty" ng-click="close()"></button><div class="title text-left">{{ \'ION-PHOTO.FILTERS\' | translate }}</div><button class="button button-clear" ng-click="submitCapture()">{{ \'ION-PHOTO.NEXT\' | translate }}</button></ion-header-bar><ion-content scroll="false"><photo-filter image="form.photo"></photo-filter></ion-content></ion-modal-view>';
            var scope    = $rootScope.$new(true);

            scope.form = {
                photo: image
            };

            scope.modal = $ionicModal.fromTemplate(template, {
                scope: scope
            });

            scope.modal.show();


            scope.submitCapture = function () {
                var canvas  = window.document.getElementById('image');
                var dataUrl = canvas.toDataURL();
                scope.close();
                defer.resolve(dataUrl);
            };

            scope.close = function () {
                scope.modal.hide();
            };

            // Cleanup the modal when we're done with it!
            scope.$on('$destroy', function () {
                scope.modal.remove();
            });


            return defer.promise;

        }

    }
})();

(function () {
    'use strict';

    angular.module('ion-photo').factory('PhotoService', PhotoServiceFactory);

    function PhotoServiceFactory($ionicActionSheet, $translate, AppConfig, Share,$cordovaActionSheet, $jrCrop, $rootScope, $ionicModal, $cordovaCamera, $cordovaCapture, $q) {

        // Default Setting
        var tempImage;
        var cordova = window.cordova;
        var setting = {
            jrCrop            : true,
            quality           : 90,
            allowEdit         : false,
            filter            : true,
            correctOrientation: true,
            targetWidth       : 300,
            targetHeight      : 300,
            saveToPhotoAlbum  : false,
            allowRotation     : false,
            aspectRatio       : 0
        };

        return {
            open  : openModal,
            crop  : cropModal,
            filter: filterModal,
            share : shareModal,
        };

        function openModal(option) {
            var defer       = $q.defer();
            var options     = {
                quality           : 80,
                aspectRatio       : 0,
                allowRotation     : false,
                allowEdit         : true,
                correctOrientation: true,
                targetWidth       : 640,
                targetHeight      : 640,
                saveToPhotoAlbum  : true,
                destinationType   : window.cordova ? Camera.DestinationType.DATA_URL : null,
                encodingType      : window.cordova ? Camera.EncodingType.JPEG : null,
                popoverOptions    : window.cordova ? CameraPopoverOptions : null,
            };
            var buttons     = [{
                text: '<i class="icon ion-ios-camera"></i>' + $translate.instant('captureLibrary')
            }, {
                text: '<i class="icon ion-images"></i>' + $translate.instant('capturePhoto')
            },
                // {text: '<i class="icon ion-ios-videocam"></i>' + $translate.instant('ION-PHOTO.VIDEO')}
            ];
            var actionSheet = $ionicActionSheet.show({
                buttons      : buttons,
                titleText    : $translate.instant('submit'),
                cancelText   : $translate.instant('cancel'),
                cancel       : buttonCancel,
                buttonClicked: buttonClicked
            });

            function buttonClicked(index) {
                console.log(index);
                actionSheet();
                capture(index, options)
                    .then(filterModal)
                    .then(function (resp) {
                        console.log('Final rest');
                        defer.resolve(resp);
                    })
                    .catch(buttonCancel);
            }


            function buttonCancel(resp) {
                actionSheet(resp);
            }

            return defer.promise;
        }

        function cropModal(image) {
            var defer = $q.defer();

            $jrCrop.crop({
                url          : image,
                aspectRatio  : setting.aspectRatio ? setting.aspectRatio : false,
                allowRotation: setting.allowRotation ? setting.allowRotation : false,
                width        : setting.width ? setting.width : setting.targetWidth,
                height       : setting.height ? setting.height : setting.targetHeight,
                cancelText   : $translate.instant('cancel'),
                chooseText   : $translate.instant('submit')
            }).then(function (canvas) {
                defer.resolve(canvas.toDataURL());
            }).catch(defer.reject);

            return defer.promise;
        }

        function shareModal(image) {
            var template =
                    '<ion-modal-view class="modal-share"> <ion-header-bar class="bar-dark"> <div class="title">{{ \'submit\' | translate }}</div> <button class="button button-positive" ng-click="modal.hide()"> <i class="icon ion-arrow-right-b"></i> </button> </ion-header-bar> <ion-content ng-cloak> <div id="image"> <img ng-src="{{form.photo}}"> <div class="title">{{ form.title }}</div></div> <ul class="list"> <li class="padding"> <button ng-repeat="social in sociais" ng-click="share(form, social)"class="button button-block button-{{ social }}"><i class="icon ion-social-{{ social }}"></i>{{ social | uppercase }} </button> </li> </ul> </ion-content> </ion-modal-view>';

            if (image === undefined) return false;
            var scope   = $rootScope.$new(true);
            var socials = [
                'facebook',
                'instagram',
                'whatsapp',
                'twitter'
            ];
            //image = image.attributes;

            scope.sociais = socials;
            scope.share   = shareSocial;
            scope.form    = {
                title: '',
                photo: image
            };

            scope.modal = $ionicModal.fromTemplate(template, {
                scope: scope
            });
            scope.modal.show();


            function shareSocial(social, form) {
                console.log('share', social, form);
                Share.share(social, {
                    text : form.title,
                    image: form.photo,
                    link : AppConfig.app.url
                });
            }


        }

        function filterModal(image) {
            var defer = $q.defer();

            function openFilter(image) {
                var templateFilter = '<ion-modal-view class="modal-capture"><ion-header-bar class="bar-light"><button class="button button-clear button-icon ion-ios-arrow-thin-left" ng-click="cropImage()"></button><div class="title">{{ \'ION-PHOTO.FILTERS\' | translate }}</div><button class="button button-icon " ng-click="submitFilter()"><i class="icon ion-ios-arrow-thin-right"></i></button></ion-header-bar><ion-content><photo-filter image="form.photo"></photo-filter></ion-content></ion-modal-view>';
                var scope          = $rootScope.$new(true);
                scope.closeFilter  = closeFilter;
                scope.submitFilter = submitFilter;
                tempImage          = image;
                scope.form         = {
                    photo: image
                };

                scope.modalFilter = $ionicModal.fromTemplate(templateFilter, {
                    scope: scope
                });

                scope.cropImage = function () {
                    scope.modalFilter.remove();
                    cropModal(tempImage)
                        .then(openFilter);
                };

                scope.modalFilter.show();

                function closeFilter() {
                    defer.reject();
                    scope.modalFilter.hide();
                }

                function submitFilter() {
                    var canvas  = window.document.getElementById('image');
                    var dataUrl = canvas.toDataURL();
                    scope.modalFilter.remove();
                    defer.resolve(dataUrl);
                }

            }

            openFilter(image);

            return defer.promise;
        }


        function capture(type, options) {
            var defer = $q.defer();

            // CAMERA
            if (type === 0) {
                getPicture(0);
            }

            // GALLERY
            if (type === 1) {
                getPicture(1);
            }

            // Video
            if (type === 2) {
                getVideo();
            }

            function getPicture(method) {
                if (method === 0 && cordova) options.sourceType = Camera.PictureSourceType.CAMERA;
                if (method === 1 && cordova) options.sourceType = Camera.PictureSourceType.PHOTOLIBRARY;

                if (cordova) {
                    $cordovaCamera.getPicture(options).then(defer.resolve, defer.reject);
                } else {
                    var fileInput = angular.element('<input type="file" accept="image/x-png, image/gif, image/jpeg" max-size="2048" />');
                    fileInput[0].click();
                    fileInput.on('change', function (evt) {
                        tempImage     = evt.currentTarget.files[0];
                        var reader    = new FileReader();
                        reader.onload = function (evt) {
                            defer.resolve(evt.target.result);
                        };
                        reader.readAsDataURL(tempImage);
                    });
                }
            }

            function getVideo() {
                $cordovaCapture.captureVideo({
                    limit   : 1,
                    duration: 5
                }).then(function (mediaFiles) {
                    tempImage = mediaFiles[0].fullPath;
                    defer.resolve(tempImage);
                }, defer.reject);
            }


            return defer.promise;
        }

        function show(params) {

            var defer = $q.defer();

            if (window.cordova) {

                var options = {
                    title                    : params.title,
                    buttonLabels             : _.map(params.options, function (item) {return item.text}),
                    addCancelButtonWithLabel : params.cancelText,
                    androidEnableCancelButton: true,
                    androidTheme             : window.plugins.actionsheet.ANDROID_THEMES.THEME_HOLO_LIGHT
                };

                $cordovaActionSheet.show(options).then(function (btnIndex) {
                    if (btnIndex !== 3) {
                        defer.resolve(btnIndex);
                    }
                    defer.reject('cancel');
                });

            } else {
                var actionSheet = $ionicActionSheet.show({
                    buttons      : params.options,
                    titleText    : params.title,
                    cancelText   : params.cancelText,
                    cancel       : function () {
                        actionSheet();
                    },
                    buttonClicked: function (btnIndex) {
                        actionSheet();
                        if (btnIndex !== 3) {
                            defer.resolve(btnIndex);
                        }
                        defer.reject('cancel');
                    }
                });

            }
            return defer.promise;
        }



    }
})();

(function () {
  'use strict';

  angular
    .module('ion-photo')
    .factory('Video', VideoFactory);

  function VideoFactory($window, $q) {
    var deferred = $q.defer();
    //https://devdactic.com/capture-and-store-videos-ionic/
    // Resolve the URL to the local file
    // Start the copy process
    function createFileEntry(fileURI) {
      $window.resolveLocalFileSystemURL(fileURI, function (entry) {
        return copyFile(entry);
      }, fail);
    }

    // Create a unique name for the videofile
    // Copy the recorded video to the app dir
    function copyFile(fileEntry) {
      var name = fileEntry.fullPath.substr(fileEntry.fullPath.lastIndexOf('/') + 1);
      var newName = makeid() + name;

      $window.resolveLocalFileSystemURL(cordova.file.dataDirectory, function (fileSystem2) {
          fileEntry.copyTo(fileSystem2, newName, function (succ) {
            return onCopySuccess(succ);
          }, fail);
        },
        fail
      );
    }

    // Called on successful copy process
    // Creates a thumbnail from the movie
    // The name is the moviename but with .png instead of .mov
    function onCopySuccess(entry) {
      var name = entry.nativeURL.slice(0, -4);
      $window.PKVideoThumbnail.createThumbnail(entry.nativeURL, name + '.png', function (prevSucc) {
        return prevImageSuccess(prevSucc);
      }, fail);
    }

    // Called on thumbnail creation success
    // Generates the currect URL to the local moviefile
    // Finally resolves the promies and returns the name
    function prevImageSuccess(succ) {
      var correctUrl = succ.slice(0, -4);
      correctUrl += '.MOV';
      deferred.resolve(correctUrl);
    }

    // Called when anything fails
    // Rejects the promise with an Error
    function fail(error) {
      console.log('FAIL: ' + error.code);
      deferred.reject('ERROR');
    }

    // Function to make a unique filename
    function makeid() {
      var text = '';
      var possible = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
      for (var i = 0; i < 5; i++) {
        text += possible.charAt(Math.floor(Math.random() * possible.length));
      }
      return text;
    }

    // The object and functions returned from the Service
    return {
      // This is the initial function we call from our controller
      // Gets the videoData and calls the first service function
      // with the local URL of the video and returns the promise
      saveVideo: function (data) {
        createFileEntry(data[0].localURL);
        return promise;
      }
    };
  }

})();

